.employee__container {
  text-align: center;
  height: 100%;
  position: relative;
}
.app-title {
  margin-bottom: 2rem;
}
.article-title {
  margin-top: 2rem;
}
.employee__article {
  padding-bottom: 20px;
  border-bottom: 2px solid var(--color-primary);
}
.employee-img {
  margin-top: 1rem;
  margin-bottom: 4rem;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
.employee-img:first-child {
  margin-top: 1rem;
  margin-bottom: 0.1rem;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
legend {
  margin-bottom: 2rem;
}
